import { 
    GET_COUNTRIES,
    GET_COUNTRIES_BY_QUERY,

} from "../actions/index"

const initialState = {
    countries : [],

  };

function rootReducer (state = initialState, {type, payload}) {
    switch(type) {
        case GET_COUNTRIES :
            return {
                ...state,
                countries : payload
            }
        case GET_COUNTRIES_BY_QUERY :
            return {
                ...state,
                countries : payload
            }
            default: return state;
    }
}

export default rootReducer;